import React from "react";
import { useGetContext } from "../Hooks/useGetContext";

const Contacto = () => {
  const { idioma } = useGetContext();

  return (
    <div className="card border-secondary m-auto" style={{ maxWidth: "35rem" }}>
      <div className="card-header card-title">
        <h3>{idioma ? "Contacto" : "Contact"}</h3>
      </div>
      <div className="card-body">
        <ul>
          <li>Douglas Alberto Ontiveros Lara.</li>
          <li>{idioma ? "Venezolano." : "Venezuelan."}</li>
          <li>
            {idioma
              ? "Vivo en Santiago de Chile."
              : "I live in Santiago de Chile."}
          </li>
          <li>
            <a
              href="mailto:douglasontiveros@gmail.com?subject=Acabo de ver tu CV"
              target="_blank"
              rel="noreferrer"
            >
              douglasontiveros@gmail.com
            </a>
          </li>
          <li>
            <a
              href="https://wa.me/56933358494"
              target="_blank"
              rel="noreferrer"
            >
              +569 33358494
            </a>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Contacto;
