import React from "react";
import { useGetContext } from "../../Hooks/useGetContext";

const GitModal = ({ img }) => {
  const { idioma } = useGetContext();

  return (
    <div className="modal fade" id="git">
      <div
        className="modal-dialog modal-sm modal-dialog-centered"
        role="document"
      >
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">Git</h5>
            <button
              type="button"
              className="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true" />
            </button>
          </div>
          <div className="modal-body bgModals">
            <img src={img} alt="git imagen" className="tecno-li" />
          </div>
          <div className="modal-footer">
            <p>
              {idioma
                ? ` El sistema de control de versiones ms usado. Git add, git
          push, branch, pull, commit, entre otros comandos nos sirven para
          llevar un control en el versionado del cdigo.`
                : ` The most used version control system. Git add, git
          push, branch, pull, commit, among other commands, are used to
          control the versioning of the code.`}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default GitModal;
