import React from "react";
import miImg from "../icons/fot.png";
import { useGetContext } from "../Hooks/useGetContext";

const Home = () => {
  const { idioma } = useGetContext();

  return (
    <section className="section">
      <img src={miImg} alt="imagen principal" className="imgHome" />
      <hr size="4" />
      <h2>
        {idioma ? "Pogramador Full Stack (MERN)" : "Full Stack Programmer"}
      </h2>
      <h3>
        {idioma
          ? "y entusiasta de la programacin en general"
          : "and general programming enthusiast"}
      </h3>
    </section>
  );
};

export default Home;
