import React from "react";
import { useGetContext } from "../../Hooks/useGetContext";

const JsModal = ({ img }) => {
  const { idioma } = useGetContext();

  return (
    <div className="modal fade" id="js">
      <div
        className="modal-dialog modal-sm modal-dialog-centered"
        role="document"
      >
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">Javascript</h5>
            <button
              type="button"
              className="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true" />
            </button>
          </div>
          <div className="modal-body bgModals">
            <img src={img} alt="javascript imagen" className="tecno-li" />
          </div>
          <div className="modal-footer">
            <p>
              {idioma
                ? ` Manejo de la API del DOM, conocimiento de
        ECMAScript 6 en adelante, conocimiento en closures, comportamiento de `
                : ` Handling of the DOM API, knowledge of
        ECMAScript 6 onwards, knowledge of closures, behavior of `}
              <i>this</i>,
              {idioma
                ? ` Callback, Promesas , async y await y ms.`
                : ` Callback, Promises, async and await and more.`}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default JsModal;
