import React, { createContext, useState } from "react";

export const Contexto = createContext();

let idiomaLocalS = localStorage.getItem("idioma");
let esEspaol;

if (!idiomaLocalS) {
  localStorage.setItem("idioma", JSON.stringify(true));
  esEspaol = true;
} else {
  esEspaol = JSON.parse(idiomaLocalS);
}

const Context = ({ children }) => {
  const [idioma, setIdioma] = useState(esEspaol);

  return (
    <Contexto.Provider value={{ idioma, setIdioma }}>
      {children}
    </Contexto.Provider>
  );
};

export default Context;
