import React from "react";
import { useGetContext } from "../Hooks/useGetContext";
import errorImg from "../icons/error.jpg";

const ExpeProf = () => {
  const { idioma } = useGetContext();

  return (
    <section className="section">
      <img src={errorImg} alt="imagen error 404" className="imgExp" />
      <h4>
        {idioma
          ? "Sin experiencia laboral aun, pero en bsqueda de una oportunidad."
          : "No work experience yet, but looking for an opportunity."}
      </h4>
    </section>
  );
};

export default ExpeProf;
