import React from "react";
import { useGetContext } from "../../Hooks/useGetContext";

const MongoModal = ({ img }) => {
  const { idioma } = useGetContext();

  return (
    <div className="modal fade" id="mongo">
      <div
        className="modal-dialog modal-sm modal-dialog-centered"
        role="document"
      >
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">MongoDB</h5>
            <button
              type="button"
              className="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true" />
            </button>
          </div>
          <div className="modal-body bgModals">
            <img src={img} alt="mongodb imagen" className="tecno-li" />
          </div>
          <div className="modal-footer">
            <p>
              {idioma
                ? ` Usando Mongoose como ODM puedo esquematizar los datos
          y consultarlos de manera fcil y organizada.`
                : `
           Using Mongoose as ODM I can schematize the data
                    and consult them in an easy and organized way.`}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MongoModal;
