import React, { useEffect } from "react";
import { useGetContext } from "../Hooks/useGetContext";
import mongo from "../icons/mongodb.png";
import js from "../icons/javascript.png";
import html from "../icons/html.png";
import git from "../icons/git.png";
import react from "../icons/react.png";
import node from "../icons/nodejs.png";
import css from "../icons/css.png";
import MongoModal from "./modalComponents/MongoModal";
import GitModal from "./modalComponents/GitModal";
import HtmlModal from "./modalComponents/HtmlModal";
import NodeModal from "./modalComponents/NodeModal";
import ReactModal from "./modalComponents/ReactModal";
import JsModal from "./modalComponents/JsModal";
import CssModal from "./modalComponents/CssModal";
import { toast } from "react-toastify";

const TecnoHabili = () => {
  const { idioma } = useGetContext();

  useEffect(() => {
    toast.info(
      idioma
        ? "Puedes hacer click en los iconos"
        : "You can click on the icons",
      { autoClose: 2000 }
    );
  }, [idioma]);

  return (
    <section className="section">
      <h2>
        {idioma ? "Tecnologas y habilidades" : "Technologies and skills"}
      </h2>
      <hr size="4" />
      <ul className="tecno-ul">
        <li className="tecno-li" data-bs-toggle="modal" data-bs-target="#mongo">
          <abbr title="MongoDB">
            <img src={mongo} alt="mongodb imagen" />
          </abbr>
        </li>
        <li className="tecno-li" data-bs-toggle="modal" data-bs-target="#git">
          <abbr title="Git">
            <img src={git} alt="git imagen" />
          </abbr>
        </li>
        <li className="tecno-li" data-bs-toggle="modal" data-bs-target="#html">
          <abbr title="Html">
            <img src={html} alt="html imagen" />
          </abbr>
        </li>
        <li className="tecno-li" data-bs-toggle="modal" data-bs-target="#node">
          <abbr title="Node.js">
            <img src={node} alt="node imagen" />
          </abbr>
        </li>
        <li className="tecno-li" data-bs-toggle="modal" data-bs-target="#react">
          <abbr title="React.js">
            <img src={react} alt="react imagen" />
          </abbr>
        </li>
        <li className="tecno-li" data-bs-toggle="modal" data-bs-target="#js">
          <abbr title="JavaScript">
            <img src={js} alt="javascript imagen" />
          </abbr>
        </li>
        <li className="tecno-li" data-bs-toggle="modal" data-bs-target="#css">
          <abbr title="CSS">
            <img src={css} alt="css imagen" />
          </abbr>
        </li>
      </ul>
      <MongoModal img={mongo} />
      <GitModal img={git} />
      <HtmlModal img={html} />
      <NodeModal img={node} />
      <ReactModal img={react} />
      <JsModal img={js} />
      <CssModal img={css} />
    </section>
  );
};

export default TecnoHabili;
