import React from "react";
import { useGetContext } from "../Hooks/useGetContext";

const Estudios = () => {
  const { idioma } = useGetContext();

  return (
    <div className="card border-secondary m-auto" style={{ maxWidth: "35rem" }}>
      <div className="card-header card-title">
        <h3>{idioma ? "Estudios" : "Studies"}</h3>
      </div>
      <div className="card-body">
        <ul>
          <li>
            {idioma
              ? "E.T.I.R. Francisco Gonzlez Guinn (Educacin media - tcnico medio en petroqumica)."
              : "E.T.I.R. Francisco Gonzlez Guinn (High school - middle-level technician in petrochemical )."}
          </li>
          <li>
            {idioma
              ? "Empece a estudiar desarrollo web con Platzi. "
              : "I started studying web development with Platzi. "}
            <a
              href="https://platzi.com/p/ontiveros/"
              target="_blank"
              rel="noreferrer"
            >
              {idioma ? "Mi perfil." : "My profile."}
            </a>
          </li>
          <li>
            {idioma
              ? "Ahora estudio con toda la informacin que el internet (w3schools, LinkedIn Learning y documentaciones) y comunidades me brinden."
              : "Now I study with all the information that the internet (w3schools, LinkedIn Learning and documentations) and communities give me."}
          </li>
          <li>
            LinkedIn
            <a
              href="https://linkedin.com/in/douglas-ontiveros-233b99227"
              target="_blank"
              rel="noreferrer"
            >
              {idioma ? " perfil" : " profile"}
            </a>
          </li>
          <li>{idioma ? "Eterno autodidacta." : "Eternal self-taught."}</li>
        </ul>
      </div>
    </div>
  );
};

export default Estudios;
