import React from "react";
import { useGetContext } from "../../Hooks/useGetContext";

const HtmlModal = ({ img }) => {
  const { idioma } = useGetContext();

  return (
    <div className="modal fade" id="html">
      <div
        className="modal-dialog modal-sm modal-dialog-centered"
        role="document"
      >
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">HTML</h5>
            <button
              type="button"
              className="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true" />
            </button>
          </div>
          <div className="modal-body bgModals">
            <img src={img} alt="html imagen" className="tecno-li" />
          </div>
          <div className="modal-footer">
            <p>
              {idioma
                ? ` Conocimientos de las etiquetas semnticas de este
          lenguaje de marcado de hipertexto que es tan importante.`
                : ` Knowledge of the semantic labels of this
          hypertext markup language that is so important.`}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default HtmlModal;
