import React from "react";
import { useGetContext } from "../../Hooks/useGetContext";

const CssModal = ({ img }) => {
  const { idioma } = useGetContext();

  return (
    <div className="modal fade" id="css">
      <div
        className="modal-dialog modal-sm modal-dialog-centered"
        role="document"
      >
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">CSS</h5>
            <button
              type="button"
              className="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true" />
            </button>
          </div>
          <div className="modal-body bgModals">
            <img src={img} alt="css imagen" className="tecno-li" />
          </div>
          <div className="modal-footer">
            <p>
              {idioma
                ? ` Selectores, especificidad , pseudo-clases,
          pseudo-elementos, media queries y ms.`
                : ` Selectors, specificity, pseudo-classes,
          pseudo-elements, media queries and more.`}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CssModal;
