import React from "react";
import { useGetContext } from "../../Hooks/useGetContext";

const ReactModal = ({ img }) => {
  const { idioma } = useGetContext();

  return (
    <div className="modal fade" id="react">
      <div
        className="modal-dialog modal-sm modal-dialog-centered"
        role="document"
      >
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">React.js</h5>
            <button
              type="button"
              className="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true" />
            </button>
          </div>
          <div className="modal-body bgModals">
            <img src={img} alt="react imagen" className="tecno-li" />
          </div>
          <div className="modal-footer">
            <p>
              {idioma
                ? ` Conocimiento en la librera ms usada en el Frontend,
          manejo de Hooks (useState, useEfecct, useContext, useReducer,
          useMemo, entre otros), Class component, componentes funcionales,
          formularios controlados y ms.`
                : ` Knowledge in the most used library in the Frontend,
          Hook handling (useState, useEfect, useContext, useReducer,
          useMemo, and so on), Class component, functional components,
          controlled forms and more.`}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ReactModal;
