import React, { useState } from "react";
import Context from "../context/Context";
import Layout from "./Layout";
import Home from "./Home";
import Contacto from "./Contacto";
import ProyPers from "./ProyPers";
import TecnoHabili from "./TecnoHabili";
import Estudios from "./Estudios";
import ExpeProf from "./ExpeProf";
import Aside from "./Aside";
import { ToastContainer } from "react-toastify";

const Main = () => {
  const [home, setHome] = useState(true);
  const [contacto, setContacto] = useState(false);
  const [proyPers, setProypers] = useState(false);
  const [tecnologias, setTecnologias] = useState(false);
  const [estudios, setEstudios] = useState(false);
  const [expeProf, setExpeProf] = useState(false);
  const [asideAppear, setAsideAppear] = useState(false);
  const [closeBtn, setCloseBtn] = useState(false);

  const functions = {
    setHome,
    setContacto,
    setProypers,
    setTecnologias,
    setEstudios,
    setExpeProf,
    asideAppear,
    setAsideAppear,
    setCloseBtn,
  };

  const handleAside = {
    asideAppear,
    setAsideAppear,
    closeBtn,
    setCloseBtn,
  };

  return (
    <Context>
      <Layout handleAside={handleAside}>
        <div className="container pt-4">
          {home && <Home />}
          <Aside functions={functions} />
          {contacto && <Contacto />}
          {proyPers && <ProyPers />}
          {tecnologias && <TecnoHabili />}
          {estudios && <Estudios />}
          {expeProf && <ExpeProf />}
          <ToastContainer />
        </div>
      </Layout>
    </Context>
  );
};

export default Main;
